/** three-dots **/
/**
 * ==============================================
 * Dot Windmill
 * ==============================================
 */
.dot-windmill, .dot-animate {
  position: relative;
  top: -10px;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background-color: #50a4ff;
  color: #50a4ff;
  transform-origin: 5px 15px;
  animation: dotWindmill 2s infinite linear; }
  .dot-windmill::before, .dot-animate::before, .dot-windmill::after, .dot-animate::after {
    content: '';
    display: inline-block;
    position: absolute; }
  .dot-windmill::before, .dot-animate::before {
    left: -8.66px;
    top: 15px;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: #50a4ff;
    color: #50a4ff; }
  .dot-windmill::after, .dot-animate::after {
    left: 8.66px;
    top: 15px;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: #50a4ff;
    color: #50a4ff; }

@keyframes dotWindmill {
  0% {
    transform: rotateZ(0deg) translate3d(0, 0, 0); }
  100% {
    transform: rotateZ(720deg) translate3d(0, 0, 0); } }
